import React, { Component} from 'react'
import Shopping from './Shopping'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Card, Button } from 'react-bootstrap';
import "./index.css";
import itemlist from './itemlist.js'

//gets quantity 
